/* modules */
.services {
  &__block {
    max-width: 360px;
    margin: 0 auto;
    position: relative;
    figure {
      position: relative;

      .img {
        position: relative;
        overflow: hidden;
        margin-bottom: 25px;
        &__background {
          position: absolute;
          z-index: 5;
          top: 0;
          left: 0;
          right: 0;
          bottom: 0;
          height: 100%;
          width: 100%;
          background: -moz-linear-gradient(left, rgba(103,112,137,0.3) 0%, rgba(243,81,79,0.2) 100%);
          background: -webkit-linear-gradient(left, rgba(103,112,137,0.3) 0%,rgba(243,81,79,0.2) 100%);
          background: linear-gradient(to right, rgba(103,112,137,0.3) 0%,rgba(243,81,79,0.2) 100%);
          // background: url('../img/img__background.png') top center no-repeat
 }          // opacity: 0.5
        &::after, &::before {
          height: 110%;
          width: 25px;
          position: absolute;
          left: -13px;
          top: -5px;
          background-color: $color-white;
          z-index: 10;
          content: '';
          transform: rotateZ(6deg); }
        &::before {
          left: inherit;
          right: -13px; }
        img {
          position: relative;
          z-index: 1; } } }

    a {
      position: absolute;
      top: 0;
      left: 0;
      height: 100%;
      width: 100%;
      z-index: 20; }

    figcaption {
      .title {
        font-size: rem(20);
        color: $link-color;
        text-transform: uppercase;
        font-weight: 400;
        transition: color 0.3s ease;
        margin-bottom: 5px; } }

    &:hover {
      figcaption {
        .title {
          color: $color-red; } } } } }

/* scew */
.block-skew {
  position: absolute;
  width: 65px;
  height: 65px;
  border: 2px solid #eeeff2;
  transform: skewX(-10deg);

  &--services {
    margin-left: -225px;
    bottom: 60px; }

  &--advantages {
    width: 250px;
    height: 250px;
    z-index: 5;
    bottom: 0px;
    margin-left: -115px;
    border-width: 4px;
    border-color: #fbfcfc;
    transform: skewX(-8deg);
    &.right {
      top: 0px;
      bottom: inherit;
      margin-left: 1020px; } }

  &--about {
    width: 125px;
    height: 125px;
    border-width: 3px;
    border-color: #eeeff2;
    margin-left: 1280px;
    &.small {
      width: 60px;
      height: 60px;
      border-width: 2px;
      margin-left: 1450px; } }

  &--doctors {
    margin-left: 1360px; }

  &--consultation {
    &.small {
      width: 60px;
      height: 60px;
      margin-left: -300px; }
    &.medium {
      border-color: #acb1bf;
      border-width: 3px;
      margin-left: -206px;
      height: 125px;
      width: 125px; }
    &.big {
      border-color: #5a647f;
      border-width: 5px;
      width: 257px;
      height: 257px;
      margin-left: -53px; } }

  &--main-slide {
    right: 0;
    z-index: 10;
    top: 290px;
    &.small {
      width: 60px;
      height: 60px;
      right: 340px; }
    &.medium {
      width: 125px;
      height: 125px;
      right: 164px;
      border-width: 3px;
      border-color: #acb1bf; }
    &.big {
      width: 257px;
      height: 257px;
      right: -150px;
      border-width: 5px;
      border-color: #5a647f; } } }

/* advantages */
.advantages {
  padding: 30px 0;
  overflow: hidden;

  &__body {
    padding: 50px 15px;
    background-color: #fbfcfc;
    margin: 0 -15px;
    position: relative;
    z-index: 10;
    &::after, &::before {
      height: 100%;
      width: 65px;
      position: absolute;
      left: -32px;
      top: 0;
      background-color: #fbfcfc;
      z-index: 5;
      content: '';
      transform: skewX(-8deg); }
    &::after {
      left: inherit;
      right: -32px; } }

  &__block {
    display: table;
    color: #636363;
    position: relative;
    z-index: 10;
    font-size: rem(13);
    &>div {
      display: table-cell;
      vertical-align: top;
      &+div {
        padding-left: 15px; }
      &::first-child {
        text-align: right;
        width: 70px; } }
    p {
      margin: 0; }
    .title {
      font-size: rem(15);
      font-weight: 500;
      text-transform: uppercase;
      color: $color-waikawa-grey;
      padding-bottom: 15px;
      margin-bottom: 15px;
      position: relative;
      &::after {
        position: absolute;
        content: '';
        left: 0;
        bottom: 0;
        width: 60px;
        height: 2px;
        background: #dbdee3; } } }

  [class*=col-] {
    @include first(3) {
      .advantages__block {
        margin-bottom: 45px; } } } }

/* about */
.about {
  overflow: hidden;
  &__title {
    font-size: rem(16);
    font-weight: 400;
    line-height: 1.4;
    margin-bottom: 25px; }

  &__block {
    display: table;
    font-size: rem(14);
    &>div {
      display: table-cell;
      vertical-align: top;
      &:first-child {
        width: 70px; } }
    p {
      line-height: 1.4; } }

  &__carousel {
    position: relative;
    overflow: hidden;
    &::after {
      height: 110%;
      width: 65px;
      position: absolute;
      right: -30px;
      bottom: -1px;
      top: -1px;
      background-color: $color-white;
      z-index: 5;
      content: '';
      transform: rotate(6deg); } }

  .section__header {
    padding-top: 50px; } }

/* doctors */
.doctors {
  overflow: hidden;
  padding-bottom: 130px;
  &__row {
    padding-top: 45px; }
  &__list {
    padding-left: 30px;
    position: relative;
    z-index: 10;
    &+.btn {
      margin-left: 30px; } }
  &__block {
    display: table;
    margin-bottom: 34px;
    &>div {
      display: table-cell;
      vertical-align: top;
      &+div {
        padding-left: 15px; } }
    .title {
      text-transform: uppercase;
      font-size: rem(16);
      font-weight: 500;
      color: $color-waikawa-grey;
      line-height: 1.2;
      margin-bottom: 5px; }
    small {
      font-size: rem(12);
      color: #999; }
    .img {
      background-color: pink;
      overflow: hidden;
      border-radius: 50%; } }
  &__img {
    position: absolute;
    margin-top: -40px;
    left: 50%;
    transform: translateX(-50%); } }

/* certificate */
.certificate {
  &__carousel {
    width: 200px;
    margin: 0 auto 65px;
    position: relative; } }

/* consultation */
.consultation {
  background: url('../img/consultation/consultation.jpg') center no-repeat;
  height: 330px;
  overflow: hidden;
  padding-top: 0;
  &__label {
    font-size: rem(38);
    text-transform: uppercase;
    font-weight: 300;
    font-style: italic;
    position: relative;
    // background-color: $color-red
    color: $color-white;
    margin-bottom: 35px;
    margin-top: 65px;
    padding-left: 55px;
    margin-left: -335px;
    width: 850px;
    letter-spacing: -1px;
    padding-top: 5px;
    padding-bottom: 7px;
    span {
      position: relative;
      z-index: 10; }
    strong {
      text-transform: none;
      font-weight: 400; }
    &::after, &::before {
      position: absolute;
      z-index: 5;
      height: 51%;
      top: 0;
      left: 0;
      transform: skewX(-10deg);
      background-color: $color-red;
      content: '';
      width: 100%; }
    &::after {
      bottom: 0;
      top: inherit;
      left: -11px;
      width: calc(100% - 130px); } } }

/* slide */
