    width: 483px;
    &::after {
      width: 0;
      position: absolute;
      right: -114px;
      top: 0;
      z-index: 5;
      content: '';
      border-right: 114px solid transparent;
      border-top: 700px solid $color-white;
      border-left: 5px solid $color-white; } }
  &__img {
    width: 1437px;
    overflow: hidden;
    .img {
      float: left; }
    .main-slide__label, .main-slide__label-bottom {
      display: none; } }

  &__label {
    font-size: rem(38);
    text-transform: uppercase;
    font-weight: 300;
    font-style: italic;
    position: absolute;
    z-index: 50;
    right: -760px;
    // background-color: $color-red
    color: $color-white;
    margin-bottom: 35px;
    margin-top: 320px;
    padding-left: 55px;
    width: 850px;
    letter-spacing: -1px;
    padding-top: 5px;
    padding-bottom: 7px;
    span {
      position: relative;
      z-index: 10; }
    strong {
      text-transform: none;
      font-weight: 400; }
    &::after, &::before {
      position: absolute;
      z-index: 5;
      height: 51%;
      top: 0;
      left: 0;
      transform: skewX(-10deg);
      background-color: $color-red;
      content: '';
      width: 100%; }
    &::after {
      bottom: 0;
      top: inherit;
      left: -11px;
      width: calc(100% - 130px); } }

  &__label-bottom {
    position: absolute;
    width: 600px;
    right: -486px;
    margin-top: 460px;
    z-index: 50;
    font-size: rem(18);
    font-style: italic;
    &>* {
      z-index: 50;
      position: relative;
      vertical-align: middle; }
    span {
      color: $color-white;
      padding-left: 60px;
      height: 57px;
      display: inline-block;
      line-height: 1.3;
