    &::after {
      position: absolute;
      z-index: 5;
      height: 100%;
      top: 0;
      left: 0;
      transform: skewX(-10deg);
      background-color: $color-red;
      content: '';
      width: 100%; }
    .icon {
      margin-left: 25px; } } }

/* introtitle */
.introtitle {
  font-size: rem(16);
  font-weight: 300;
  line-height: 1.4;
  margin-bottom: 25px; }

.table {
  td {
    p {
      margin-bottom: 0;
      &+p {
        margin-top: 15px; } } }
  &--color {
    margin-bottom: 40px;
    &>thead {
      &>tr {
        &>th {
          background-color: #848a9b;
          color: $color-white;
          font-size: rem(15);
          text-transform: uppercase;
          border-bottom: 0;
          padding: 10px 20px 8px 20px;
          font-weight: 300; } } }
    &>tbody {
      &>tr {
        &>td {
          padding: 10px 20px 8px 20px;
          border-top: 0;
          border-bottom: 1px solid #f0f0f0; } } } } }

/* services */
.services {
  &--list {
    margin-bottom: 30px; }
  &__head {
    color: $color-white;
    background-color: $color-red;
    text-align: center;
    padding: 17px 5px;
    text-transform: uppercase;
    position: relative;
    transform: skewX(-6deg);
    &__body {
      transform: skewX(6deg); } }

  &__block {
    &--list {
      display: table;
      margin-bottom: 30px;
      &>div {
        display: table-cell;
        vertical-align: top;
        &+div {
          padding-left: 10px; } }
      figure {
        width: 165px;
        .img {
          margin-bottom: 0; } }
      figcaption {
        .title {
          font-size: rem(18);
          word-break: break-all; }
        small {
          line-height: 1.2;
          display: inline-block; } } } } }

/* .all-articles */
.all-articles {
  color: $color-red;
  text-decoration: none;
  font-size: rem(12);
  &:hover {
    text-decoration: underline; }
  i {
    margin-right: 10px;
    font-size: rem(14);
    color: #bebebe;
    vertical-align: middle; } }

/* specialist */
.specialist {
  &__block {
    text-decoration: none;
    background-color: #fafafa;
    padding: 15px;
    display: table;
    margin-bottom: 25px;
    color: #444444;
    font-size: rem(14);
    p {
      margin: 0;
      &+p {
        margin-top: 15px; } }
    &>div {
      display: table-cell;
      vertical-align: top;
      &+div {
        padding-left: 15px; } }
    .img {
      border-radius: 50%;
      display: block; }
    &:hover, &:focus, &:active {
      color: #444; }
    [class*=ion-] {
      position: absolute;
      font-size: rem(15);
      color: $color-red;
      margin-top: -5px;
      margin-left: -5px; }
    .ion-minus-circled {
      opacity: 1; }
    .ion-plus-circled {
      opacity: 0; }
    &[aria-expanded='false'] {
      .ion-minus-circled {
        opacity: 0; }
      .ion-plus-circled {
        opacity: 1; } } }
  &__title {
    font-size: rem(18);
    text-transform: uppercase;
    color: $header-color;
    font-weight: 500;
    margin-bottom: 20px;
    &+small {
      font-size: rem(12);
      color: #999999;
      font-weight: 300;
      top: -20px;
      margin-bottom: 0;
      display: inline-block;
      position: relative; } } }

/* comments */
.comments {
  padding-bottom: 10px;
  &__row {
    border-bottom: 1px solid #fafafa;
    margin-bottom: 15px;
    padding-bottom: 15px;
    overflow: hidden;
    p {
      margin: 0;
      &+p {
        margin-top: 10px; } }
    &:first-child {
      border-top: 1px solid #fafafa;
      padding-top: 15px; } }
  &__title {
    display: table;
    width: 100%;
    margin-bottom: 5px;
    &>div {
      display: table-cell;
      vertical-align: middle;
      &:last-child {
        text-align: right; } }
    strong {
      font-size: rem(15); } }
  .city {
    color: $color-red;
    font-size: rem(12); } }

/* pagination */
.pagination {
  text-align: center;
  color: $color-white;
  font-size: rem(12);
  margin-bottom: 40px;
  &__body {
    display: inline-block;
    transform: skewX(-6deg);
    background-color: $color-waikawa-grey;
    padding: 0 20px;
    &>ul {
      display: block;
      transform: skewX(6deg);
      white-space: nowrap;
      padding: 0;
      margin: 0;
      &>li {
        margin: 0;
        list-style: none;
        display: inline-block;
        &>a, &>span {
          text-decoration: none;
          color: $color-white;
          display: inline-block;
          transform: skewX(-6deg);
          height: 30px;
          line-height: 30px;
          padding: 0 10px;
          &>span {
            transform: skewX(6deg);
            display: block; } }
        &>a {
          &:hover {
            text-decoration: underline; } }
        &.active {
          &>span, &>a {
            background-color:  #5a647f; } } } } } }

/* google map */
#map_canvas {
  position: relative;
  overflow: hidden;
  height: 0px;
  padding-bottom: 56%;
  background: $color-white;
  margin-bottom: 20px;
  border: 5px solid #ececec; }

.google-map {
  position: relative;
  height: 0px;
  padding-bottom: 56%;
  background: $color-white;
  border: 5px solid #ececec;
  margin-bottom: 20px;
  iframe {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%; } }

/* articles */
.articles {
  margin-bottom: 25px;
  &__row {
    display: table;
    padding: 25px 0;
    border-bottom: 1px solid #fafafa;
    width: 100%;
    &:first-child {
      border-top: 1px solid #fafafa; }
    &>div {
      display: table-cell;
      vertical-align: top;
      &+div {
        padding-left: 30px; } }
    .img {
      max-width: 260px;
      width: 260px; } }
  &__title {
    text-transform: uppercase;
    margin-bottom: 10px;
    font-weight: 500;
    color: #636363; }
  .btn {
    text-transform: none;
    font-weight: 300; }
  .btn--add {
    &::before {
      background-color: #848a9b;
      border-color: #848a9b; } } }

/* page404 */
.page404 {
  position: absolute;
  right: -340px;
  top: 45px;
  &__body {} }

.back {
  display: inline-block;
  background-color: $color-red;
  color: $color-white;
  padding: 0 10px;
  border-radius: 10px;
  text-decoration: none;
  border: 1px solid $color-red;
  &:hover {
    color: $color-white;
    background-color: $color-white;
    color: $color-red; } }

/* services__nav */
