  &__head {
    position: relative;

    .img {
      position: relative;
      overflow: hidden;
      margin-bottom: 15px;
      img {
        position: relative;
        z-index: 1;
        max-width: 100%;
        display: block;
        height: auto; }
      &::before, &::after {
        height: 100%;
        width: 25px;
        position: absolute;
        left: -13px;
        top: 0;
        background-color: #fff;
        z-index: 10;
        content: '';
        transform: skewX(-6deg); }
      &::before {
        left: inherit;
        right: -13px; }
      &__background {
        position: absolute;
        z-index: 5;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        height: 100%;
        width: 100%;
        background: -moz-linear-gradient(left, rgba(103,112,137,0.3) 0%, rgba(243,81,79,0.2) 100%);
        background: -webkit-linear-gradient(left, rgba(103,112,137,0.3) 0%,rgba(243,81,79,0.2) 100%);
        background: linear-gradient(to right, rgba(103,112,137,0.3) 0%,rgba(243,81,79,0.2) 100%); } } } }

