        font-size: rem(16);
        font-weight: 400;
        padding: 10px 0;
        &::after {
          position: absolute;
          width: 0;
          height: 0;
          top: -10px;
          content: '';
          border-left: 10px solid rgba(0, 0, 0, 0);
          border-right: 10px solid rgba(0, 0, 0, 0);
          border-bottom: 10px solid #fafafa;
