        .icon--advantage-6-2 {
          @include sprite($icon-advantage-6-2-small); }
        .icon--advantage-7 {
          @include sprite($icon-advantage-7-small); }
        .icon--advantage-4-2 {
          @include sprite($icon-advantage-4-2-small); }
        .icon--advantage-2-2 {
          @include sprite($icon-advantage-2-2-small); } } } }

  .services {
    &__block {
      &--list {
        &>div {
          &+div {
            padding-left: 0; } }
        figcaption {
          .title {
            font-size: rem(18); }
          small {
            font-size: rem(13); } } } } }

  .form {
    &--block {
      padding: 0 15px 20px 15px; }
    &__head {
      margin: -5px -20px 15px -20px; }

    &--comment {
      .btn--form {
