  .header {
    &__bottom {
      display: block;
      text-align: center;
      &>div {
        display: block; } }
    &__call {
      max-width: 450px;
      margin: 0 auto; } }

  .logo {
    margin-bottom: 15px; }

  .main-slide {
    &__body {
